<template>
    <div class="main-content ">
        <div class="breadcrumbs" id="breadcrumbs">
            <ul class="breadcrumb">
                <li>
                    <i class="icon-home home-icon"></i>
                    <a href="#"></a>
                </li>
                <li class="active">微信息</li>
            </ul><!-- .breadcrumb -->
        </div>
        <div class="page-content cs-main cs-page-content" id="page-content" :style="mapStyle">
            
            <div v-show="dialogImgVisible == true" >
                <div class="cs-wxx-btn-backmain" @click="backtomain"></div>
                <el-carousel indicator-position="none" :height="el_carousel_height" >
                    <el-carousel-item v-for="(item, index) in items" :style="carousel_style">
                      <img :src="item.url" :alt="item.name" class="image" >
                    </el-carousel-item>
                </el-carousel>
            </div>
            <div v-if="dialogImgVisible == false">
                <el-carousel :interval="4000" type="card" height="300px">
                    <el-carousel-item v-for="(item, index) in items">
                        <div @click="openImgDialog(item, index)">
                            <img :src="item.url" :alt="item.name" class="image">
                        </div>
                    </el-carousel-item>
                </el-carousel>            
                
                <div >
                    <el-row >
                      <el-col :span="6" v-for="(item2, index) in items2">
                        <div @click="openCardDialog(item2, index)" >
                            <el-card :body-style="{ padding: '0px' }">
                              <a href="javascript:;"><img :src="item2.url" class="image"></a>
                              <div class="cs-card-name">
                                <span>{{item2.name}}</span>
                              </div>
                            </el-card>
                        </div>
                      </el-col>
                    </el-row>
                    <el-dialog v-model="dialogCardVisible"> 
                        <div class="cs-card-info">                             
                          <h1>{{selectCard.title}}</h1>
                          <h4 class="subtitle">发布人：{{selectCard.autor}} &nbsp;&nbsp;&nbsp;&nbsp; {{selectCard.date}}</h4>
                          <img :src="selectCard.url" class="image">
                          <p class="desc">{{selectCard.desc}}</p>
                        </div>                
                    </el-dialog>
                </div>
            
            </div>

            

        </div>
    </div>
</template>
<script>
    export default {
        data: function() {
            return {
               items: [{
                    url: "./src/img/3d_room.jpg",
                    name: "1"
               }, {                    
                    url: "./src/img/building.jpg",
                    name: "2"
               }, {                    
                    url: "./src/img/hk.jpg",
                    name: "3"
               }, {                    
                    url: "./src/img/lion.jpg",
                    name: "4"
               }, {                    
                    url: "./src/img/triworks_abstract23.jpg",
                    name: "5"
               }, {                    
                    url: "./src/img/vancouver.jpg",
                    name: "6"
               }],
               dialogImgVisible: false,               
               items2: [{
                    url: "./src/img/3d_room.jpg",
                    name: "1"
               }, {                    
                    url: "./src/img/building.jpg",
                    name: "2"
               }, {                    
                    url: "./src/img/hk.jpg",
                    name: "3"
               }, {                    
                    url: "./src/img/lion.jpg",
                    name: "4"
               }],               
               dialogCardVisible : false,
               selectCard:{
                    title: "",
                    date: "",
                    autor: "",
                    url: "",
                    desc: ""  
               },
               cards: [{
                    title: "XX社区业主活动中心楼板开裂，重新现浇",
                    date: "2017年2月21日",
                    autor: "李慧萍",
                    url: "./src/img/lion.jpg",
                    desc: "XX社区业主活动中心楼板开裂，重新现浇，业主活动中心3月15日前关闭，原定活动一律顺延，给广大业主带来不，敬请谅解！"  
               },{
                    title: "2XX社区业主活动中心楼板开裂，重新现浇",
                    date: "2017年2月21日",
                    autor: "李慧萍",
                    url: "./src/img/lion.jpg",
                    desc: "XX社区业主活动中心楼板开裂，重新现浇，业主活动中心3月15日前关闭，原定活动一律顺延，给广大业主带来不，敬请谅解！"   
               },{
                    title: "3XX社区业主活动中心楼板开裂，重新现浇",
                    date: "2017年2月21日",
                    autor: "李慧萍",
                    url: "./src/img/lion.jpg",
                    desc: "XX社区业主活动中心楼板开裂，重新现浇，业主活动中心3月15日前关闭，原定活动一律顺延，给广大业主带来不，敬请谅解！"   
               },{
                    title: "4XX社区业主活动中心楼板开裂，重新现浇",
                    date: "2017年2月21日",
                    autor: "李慧萍",
                    url: "./src/img/lion.jpg",
                    desc: "XX社区业主活动中心楼板开裂，重新现浇，业主活动中心3月15日前关闭，原定活动一律顺延，给广大业主带来不，敬请谅解！"   
               }],
               mapStyle:{},
               carousel_style:{},
               dialog_height: 500,
               el_carousel_height : "600px"
            }
        },

        created: function() {
            let that = this;
            //that.resetPage();
            window.onresize = function(){
                that.resetPage();
            }
        },
        methods: {
            openImgDialog: function (item, index) {
                this.dialogImgVisible = true;
                //ElCarousel.setActiveItemindex();
                this.$broadcast('setActiveItemindex', index)
            },
            backtomain : function () {
                this.dialogImgVisible = false;
            },
            openCardDialog: function (item2, index) {
                this.dialogCardVisible = true;
                this.selectCard = this.cards[index]
            },
            resetPage: function() {
                let mapHeight = document.documentElement.offsetHeight - 40;
                this.mapStyle = {
                    height:mapHeight+'px'
                }
                let _dialog_height = mapHeight;
                this.carousel_style = {
                    height:_dialog_height+'px'
                }

                this.el_carousel_height = _dialog_height + "px";
            }
        }
        
    } 
</script>
<style>  
.el-carousel__item h3 {color:#475669;font-size:14px;opacity:0.75;line-height:200px;margin:0;}
.el-carousel__item:nth-child(2n) {background-color:#99a9bf;}
.el-carousel__item:nth-child(2n+1) {background-color:#d3dce6;}
.cs-card-name{padding: 8px 0;text-align:center;background:#000;color:#fff;}
.image {width:100%;display:block;}
.cs-main{padding-top:20px;}
.cs-main .el-row{margin:10px -10px 0;padding-top: 20px;border-top:1px solid #e8e8e8;}
.cs-main .el-col{padding:0 10px;}
.cs-card-info .subtitle{text-align: center;}
.cs-card-info .desc{text-indent: 2em}
.cs-main .el-carousel__item--card{-width:90%;}
.cs-wxx-btn-backmain{position: absolute;right: 20px;height: 50px;width: 50px;z-index: 2000;
    background: url('../../img/exit.png') no-repeat 0 0;cursor: pointer;background-size: cover;}

</style>



